.mapPageContainer {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.middle {
    flex-grow: 1;
    overflow-y: auto;
    min-height: 0px;
    background-color: orange;
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100%;
}

.map {
    flex-grow: 1;
    z-index: 10;
    height: 90vh;
    height: 100vh;
}

